<template>
  <div>
    <v-card>
      <v-card-title>
        <h1>Register</h1>
      </v-card-title>
      <v-card>
        <v-form>
          <v-text-field label="Name" v-model="user.name">Name</v-text-field>
          <v-text-field label="Alamat" v-model="user.alamat">Alamat</v-text-field>
          <v-text-field label="Program" v-model="user.program">Program</v-text-field>
          <button @click="handleSubmit">submit</button>
        </v-form>
      </v-card>
    </v-card>
  </div>
</template>


<script>
export default {
  data: () => {
    return {
      user: {
        name: "",
        alamat: "",
        program: ""
      }
    };
  },
  methods: {
    handleSubmit() {
      this.$store.commit("getUser", this.user);
      this.$router.push("frontend");
    }
  }
};
</script>